import React, { useState } from "react";
import { Button, FormControl, InputGroup } from "react-bootstrap";
import { useHistory } from "react-router-dom";
import readHook from "../hooks/readHook";
import axios from "axios";
import Popup from "./Popup";
import { useForm } from "react-hook-form";

function BoardModify({ location }) {
  const history = useHistory();
  const { id } = location.state;
  const result = readHook(`http://lukeboard.herokuapp.com/board/${id}`);
  const { title, desc } = result;
  const { register, handleSubmit } = useForm();

  const [popup, setPopup] = useState({
    open: false,
    motitle: "",
    message: "",
    callback: false,
    color: "",
    del: false,
  });

  const onSubmit = (data) => {
    axios
      .put(`http://lukeboard.herokuapp.com/board/${id}`, {
        ...result,
        title: data.title,
        desc: data.desc,
      })
      .then(
        setPopup({
          open: true,
          motitle: "",
          message: " .",
          callback: redirect,
          color: "primary",
          del: false,
        })
      );
  };

  const onDelete = () => {
    setPopup({
      open: true,
      motitle: "   ",
      message: " ?",
      callback: startDelete,
      color: "danger",
      del: true,
    });
  };

  const startDelete = () => {
    axios.delete(`http://lukeboard.herokuapp.com/board/${id}`).then(
      setPopup({
        open: true,
        motitle: "",
        message: " .",
        callback: redirect,
        color: "primary",
        del: false,
      })
    );
  };

  const redirect = () => {
    history.push("/");
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <div className="mb-4">
        <h2> </h2>
      </div>
      <InputGroup className="mb-2">
        <InputGroup.Text></InputGroup.Text>
        <FormControl type="text" defaultValue={title} {...register("title")} />
      </InputGroup>
      <InputGroup className="mb-2">
        <InputGroup.Text></InputGroup.Text>
        <FormControl
          as="textarea"
          defaultValue={desc}
          {...register("desc")}
          style={{ height: "10rem", resize: "none" }}
        />
      </InputGroup>
      <Button variant="danger float-start" onClick={onDelete}>
        
      </Button>
      <Button variant="secondary" className="float-end" type="submit">
        
      </Button>
      <Button
        variant="outline-secondary float-end me-2"
        onClick={() => history.push(`/read/${id}`)}
      >
        
      </Button>
      <Popup popup={popup} setpopup={setPopup} />
    </form>
  );
}

export default BoardModify;
